import axios from 'axios';
import { useState } from 'react';
import AddPopup from '../popup';

export default function Createsupplier() {
    const apiKey = "https://backend-mqr6.onrender.com";

    const [productdata, setproductdata] = useState({ name: "", GSTIN: "", mobile: "", addresh: "" });
    const [errors, setErrors] = useState({});
    const [errmassage, seterrmassage] = useState('')

    const validate = () => {
        const newErrors = {};
        if (!productdata.name) newErrors.name = "Supplier name is required";
        if (!productdata.GSTIN) newErrors.GSTIN = "GSTIN is required";
        if (!productdata.mobile) newErrors.mobile = "Mobile number is required";
        if (!productdata.addresh) newErrors.addresh = "Address is required";
        return newErrors;
    };

    const addsupplier = async () => {
      const validationErrors = validate();
      if (Object.keys(validationErrors).length) {
          setErrors(validationErrors);
          return;
      }
  
      try {
          const { name, GSTIN, mobile, addresh } = productdata;
          const token = localStorage.getItem('token');
          await axios.post(
              `${apiKey}/api/supplier/addsupplier`,
              { name, GSTIN, mobile, addresh },
              { headers: { "authtoken": token } }
          )
          .then(response => {
              if (response.data.success) {
                  seterrmassage(
                      <>
                          <p className="text-green-600 font-semibold text-lg mb-2">{response.data.message}</p>
                          <div className="relative w-full h-2 rounded-full overflow-hidden bg-green-200 mt-4">
                              <div className="absolute top-0 left-0 h-full bg-green-500 animate-progress-bar2" style={{ animationDuration: '0.5s' }}></div>
                          </div>
                      </>
                  );
                  handleAddItem(); // Ensure this function is defined
                  // Clear form data after successful submission
                  setproductdata({ name: "", GSTIN: "", mobile: "", addresh: "" });
              } else {
                  seterrmassage(
                      <>
                          <p className="text-red-600 font-semibold text-lg mb-2">{response.data.message}</p>
                          <div className="relative w-full h-2 rounded-full overflow-hidden bg-red-200 mt-4">
                              <div className="absolute top-0 left-0 h-full bg-red-500 animate-progress-bar2" style={{ animationDuration: '0.5s' }}></div>
                          </div>
                      </>
                  );
                  handleAddItem(); // Ensure this function is defined
              }
          })
          .catch(err => console.log(err));
      } catch (error) {
          console.error('Error:', error);
      }
  };
  

    const onchange = (e) => {
        setproductdata({ ...productdata, [e.target.name]: e.target.value });
    };
    const [showPopup, setShowPopup] = useState(false);

    const handleAddItem = () => {
        // Simulate an add item action
        setShowPopup(true);
    };
  
    const handleClosePopup = () => {
        setShowPopup(false);
    };

    return (
        <>
            <div className=" ">
            <div className="App">
            
            <AddPopup show={showPopup} onClose={handleClosePopup} message={errmassage}  />
       </div>
                <div className="mt-4 lg:w-full">
                    <div className="bg-[#f2f2f2] grid md:grid-cols-2 text-black px-2 gap-6 md:gap-12 items-center">
                        <div className="relative w-[100%]">
                            <input
                                type='text'
                                name='name'
                                onChange={onchange}
                                value={productdata.name}
                                placeholder='Supplier name'
                                className={`px-3 w-[100%] py-1 border-[2px] rounded-md ${errors.name ? 'border-red-500' : 'border-black'}`}
                            />
                            {errors.name && <p className="text-red-500 text-sm absolute top-full left-0">{errors.name}</p>}
                        </div>
                        <div className="relative w-[100%]">
                            <input
                                type='text'
                                name='GSTIN'
                                onChange={onchange}
                                value={productdata.GSTIN}
                                placeholder='GSTIN'
                                className={`px-3 py-1 w-[100%] border-[2px] rounded-md ${errors.GSTIN ? 'border-red-500' : 'border-black'}`}
                            />
                            {errors.GSTIN && <p className="text-red-500 text-sm absolute top-full left-0">{errors.GSTIN}</p>}
                        </div>
                        <div className="relative w-[100%]">
                            <input
                                type='text'
                                name='mobile'
                                onChange={onchange}
                                value={productdata.mobile}
                                placeholder='Mobile'
                                className={`px-3 py-1 w-[100%] border-[2px] rounded-md ${errors.mobile ? 'border-red-500' : 'border-black'}`}
                            />
                            {errors.mobile && <p className="text-red-500 text-sm absolute top-full left-0">{errors.mobile}</p>}
                        </div>
                        <div className="relative w-[100%]">
                            <input
                                type='text'
                                name='addresh'
                                onChange={onchange}
                                value={productdata.addresh}
                                placeholder='Address'
                                className={`px-3 py-1 w-[100%] border-[2px] rounded-md ${errors.addresh ? 'border-red-500' : 'border-black'}`}
                            />
                            {errors.addresh && <p className="text-red-500 text-sm absolute top-full left-0">{errors.addresh}</p>}
                        </div>
                    </div>
                    <div className='flex justify-center mt-5 w-full'>
                        <button
                            onClick={addsupplier}
                            className="bg-blue-500 text-white font-bold py-2 px-4 w-[50%] rounded hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50"
                        >
                            Add Supplier
                        </button>
                    </div>
                </div>
            </div>
        </>
    );
}
