import { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import AddPopup from '../popup';

export default function CreateProducts() {
  const [productdata, setproductdata] = useState({
    name: "",
    othername: "",
    category: "",
    MRP: "",
    salingprice: "",
    Cost: "",
    Serialrequired: ""
  });
  const [errmassage, seterrmassage] = useState('')

  const [serialInput, setSerialInput] = useState('');
  const [serialNumbers, setSerialNumbers] = useState([]);
  const [formErrors, setFormErrors] = useState({});
  const [categories, setCategories] = useState([]);
  const [filteredCategories, setFilteredCategories] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedcat, setselectedcat] = useState('');
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const apiKey = "https://backend-mqr6.onrender.com";

  // Create refs for input fields
  const nameRef = useRef(null);
  const categoryRef = useRef(null);
  const MRPRef = useRef(null);
  const salingpriceRef = useRef(null);
  const serialInputRef = useRef(null);
  const SerialrequiredRef = useRef(null);

  const handleChange = (e) => {
    setSerialInput(e.target.value);
  };

  const handleAddSerial = () => {
    if (serialInput.trim() && !serialNumbers.includes(serialInput.trim())) {
      setSerialNumbers([...serialNumbers, serialInput.trim()]);
      setSerialInput('');
    }
  };

  const handleKeyPress = (e, nextRef) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      nextRef.current.focus(); // Move focus to the next input
    }
  };

  const handleRemoveSerial = (serialToRemove) => {
    setSerialNumbers(serialNumbers.filter(serial => serial !== serialToRemove));
  };

  const validateForm = () => {
    const errors = {};
    const { name, category, MRP, salingprice, Serialrequired } = productdata;

    if (!name.trim()) errors.name = 'Product name is required';
    if (!category.trim()) errors.category = 'Category is required';
    if (!MRP.trim() || isNaN(MRP)) errors.MRP = 'Valid MRP is required';
    if (!salingprice.trim() || isNaN(salingprice)) errors.salingprice = 'Valid selling price is required';
    if (!Serialrequired) errors.Serialrequired = 'Serial requirement status is required';

    setFormErrors(errors);
    return Object.keys(errors).length === 0;
  };

  const addproduct = async () => {
    if (!validateForm()) return;
    try {
      const { name, category, MRP, salingprice, Serialrequired } = productdata;
      let name1 = name.toString();
      let Category1 = category.toString();
      let MRP1 = MRP.toString();
      let salingprice1 = salingprice.toString();
      let Serialrequired1 = Serialrequired.toString();
      let token = localStorage.getItem('token');

      let response = await axios.post(
        `${apiKey}/api/product/addproduct`,
        { name: name1, category: Category1, MRP: MRP1, salingprice: salingprice1, othername: serialNumbers, Serialrequired: Serialrequired1 },
        { headers: { "authtoken": token } }
      );


      if(response.data.success){
        seterrmassage(
          <>
        <p className="text-green-600 font-semibold text-lg mb-2">{response.data.message}</p>
                <div className="relative w-full h-2 rounded-full overflow-hidden bg-green-200 mt-4">
                    <div className="absolute top-0 left-0 h-full bg-green-500 animate-progress-bar2" style={{ animationDuration: '0.5s' }}></div>
                </div>
          </>
      );
      handleAddItem( );
 
       }else{
        
         seterrmassage(  <>
          <p className="text-red-600 font-semibold text-lg mb-2">{response.data.message}</p>
                <div className="relative w-full h-2 rounded-full overflow-hidden bg-red-200 mt-4">
                    <div className="absolute top-0 left-0 h-full bg-red-500 animate-progress-bar2" style={{ animationDuration: '0.5s' }}></div>
                </div>
            </>);
       handleAddItem();

     }  
      // Handle response
      setproductdata({
        name: "",
        othername: "",
        category: "",
        MRP: "",
        salingprice: "",
        Serialrequired: ""
      });
     } catch (error) {
      console.error('Error:', error);
    }
  };

  const onchange = (e) => {
    setproductdata({ ...productdata, [e.target.name]: e.target.value });
  };

  const handleChanges = (e) => {
    setproductdata({
      ...productdata,
      Serialrequired: e.target.value,
    });
  };

  const handleCategoryClick = (category) => {
    setselectedcat(category.name)
    setproductdata({ ...productdata, category: category._id });
    setDropdownOpen(false); // Close dropdown after selection
  };

  const token = localStorage.getItem('token');

  useEffect(() => {
    const fetchCategories = async () => {
      try {
        const response = await axios.get(`${apiKey}/api/category/fetchallcategory`, {
          headers: {
            'authtoken': token
          }
        });
        setCategories(response.data);
      } catch (error) {
        console.error('Error fetching categories:', error);
      }
    };
  
    fetchCategories();
  }, []);
  

  useEffect(() => {
    // Filter categories based on search query
    setFilteredCategories(
      categories.filter(category =>
        category.name.toLowerCase().includes(searchQuery.toLowerCase())
      )
    );
  }, [searchQuery, categories]);

  const options = [
    { value: 'YES', label: 'YES' },
    { value: 'NO', label: 'NO' },
  ];

  const [showPopup, setShowPopup] = useState(false);

  const handleAddItem = () => {
      // Simulate an add item action
      setShowPopup(true);
  };

  const handleClosePopup = () => {
      setShowPopup(false);
  };
  return (
    <>
      <div className="">
      <div className="App">
            
            <AddPopup show={showPopup} onClose={handleClosePopup} message={errmassage}  />
       </div>
        <div className="mt-4 lg:w-full">
          <div className='text-[12px] ml-2 text-red-600'>
            {/* Display error message */}
          </div>
          <div className="bg-[#f2f2f2] grid md:grid-cols-2 text-black px-2 gap-6 md:gap-6 items-start">
            <div>
              <input
                type='text'
                name='name'
                onChange={onchange}
                value={productdata.name}
                placeholder='Product Name'
                ref={nameRef}
                onKeyPress={(e) => handleKeyPress(e, categoryRef)}
                className={`px-3 py-1 border-black w-[100%] border-[2px] rounded-md ${formErrors.name ? 'border-red-500' : ''}`}
              />
              {formErrors.name && <p className="text-red-500 text-xs">{formErrors.name}</p>}
            </div>
            <div>
              <input
                type='text'
                name='MRP'
                onChange={onchange}
                value={productdata.MRP}
                placeholder='MRP'
                ref={MRPRef}
                onKeyPress={(e) => handleKeyPress(e, salingpriceRef)}
                className={`px-3 py-1 border-black w-[100%] border-[2px] rounded-md ${formErrors.MRP ? 'border-red-500' : ''}`}
              />
              {formErrors.MRP && <p className="text-red-500 text-xs">{formErrors.MRP}</p>}
            </div>
            <div>
              <input
                type='text'
                name='salingprice'
                onChange={onchange}
                value={productdata.salingprice}
                placeholder='Selling Price'
                ref={salingpriceRef}
                onKeyPress={(e) => handleKeyPress(e, serialInputRef)}
                className={`px-3 py-1 border-black w-[100%] border-[2px] rounded-md ${formErrors.salingprice ? 'border-red-500' : ''}`}
              />
              {formErrors.salingprice && <p className="text-red-500 text-xs">{formErrors.salingprice}</p>}
            </div>
           
            
              <div className='relative'>
                <button
                  type='button'
                  onClick={() => setDropdownOpen(!dropdownOpen)}
                  className={`  px-3 py-1 border-black border-[2px] rounded-md w-[100%] ${formErrors.category ? 'border-red-500' : ''}`}
                >
                  {productdata.category ? `Selected: ${selectedcat}` : 'Select Category'}
                </button>
                {dropdownOpen && (
                  <div className="absolute z-10 bg-white border border-gray-300 rounded-md shadow-lg w-full mt-2">
                    <input
                      type="text"
                      placeholder="Search categories..."
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      className="w-full px-3 py-2 border-b border-gray-300 rounded-t-md"
                    />
                    <div className="max-h-60 overflow-y-auto">
                      {filteredCategories.map((category) => (
                        <button
                          key={category._id}
                          onClick={() => handleCategoryClick(category)}
                          className="w-full px-3 py-2 text-left hover:bg-gray-200"
                        >
                          {category.name}
                        </button>
                      ))}
                    </div>
                  </div>
                )}
                {formErrors.category && <p className="text-red-500 text-xs">{formErrors.category}</p>}
              </div>
              <div className="relative">
                <input
                  type='text'
                  onChange={handleChange}
                  onKeyPress={(e) => handleKeyPress(e, SerialrequiredRef)}
                  value={serialInput}
                  name='serialNumbers'
                  placeholder="Enter othername"
                  ref={serialInputRef}
                  className={`px-3 py-1 w-[100%] border-black border-[2px] rounded-md ${formErrors.serialNumbers ? 'border-red-500' : ''}`}
                />
                <button
                  type="button"
                  className='w-[100px]'
                  onClick={handleAddSerial}
                >
                  Add Name
                </button>
                {formErrors.serialNumbers && <p className="text-red-500 text-xs">{formErrors.serialNumbers}</p>}
                {serialNumbers.length > 0 && (
                  <div className="mt-4">
                    <h2 className="font-bold">Other name</h2>
                    <ul className="grid gap-2 w-[50%] mt-2">
                      {serialNumbers.map((serial, index) => (
                        <li key={index} className="flex justify-between bg-gray-200 p-2 rounded-md">
                          <span>{serial}</span>
                          <button
                            onClick={() => handleRemoveSerial(serial)}
                            className="text-red-500 hover:text-red-700"
                          >
                            Remove
                          </button>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
              <div>

              <select
                name='Serialrequired'
                onChange={handleChanges}
                value={productdata.Serialrequired}
                ref={SerialrequiredRef}
                className={`px-3 py-1 border-black w-[100%] border-[2px] rounded-md ${formErrors.Serialrequired ? 'border-red-500' : ''}`}
                >
                <option value="">Serial Required</option>
                {options.map(option => (
                  <option key={option.value} value={option.value}>
                    {option.label}
                  </option>
                ))}
              </select>
              {formErrors.Serialrequired && <p className="text-red-500 text-xs">{formErrors.Serialrequired}</p>}
                    </div>
            </div>
          </div>
          <div className='w-[100%] flex justify-center'>

            <button
              type='button'
              onClick={addproduct}
              className="mt-4 px-4 py-2 bg-blue-500 w-[50%] text-white rounded-md"
              >
              Add Product
            </button>
              </div>
        </div>
      
    </>
  );
}
